"use client";

import Link from "next/link";
import {
  ShoppingBag,
  User,
  Menu,
  Search,
  Settings,
  LogOut,
  MapPin,
  Package,
} from "lucide-react";
import { useEffect, useState } from "react";
import { PromoBanner } from "./promo-banner";
import { useAuth } from "@/lib/auth-context";

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105";

// Tipos da API (CategoryResponseDto)
interface CategoryApi {
  id: string;
  name: string;
  description: string | null;
  isActive: boolean;
  createdAt: string;
}

// Modelo usado no header
interface Category {
  id: string;
  name: string;
  slug: string;
  createdAt: string;
}

// Mesmo helper de slug usado na homepage/admin
const generateSlug = (nome: string) => {
  return nome
    .toLowerCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/(^-|-$)/g, "");
};

// Pequeno helper para corrigir nomes com caracteres quebrados (AlvÃ¡ro -> Álvaro)
function fixEncoding(value?: string): string {
  if (!value) return "";
  try {
    const decoded = decodeURIComponent(escape(value));
    return decoded;
  } catch {
    return value;
  }
}

export function Header() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [profileMenuOpen, setProfileMenuOpen] = useState(false);
  const [categories, setCategories] = useState<Category[]>([]);
  const [loadingCategories, setLoadingCategories] = useState(false);

  const { user, logout, isAdmin } = useAuth();

  // Normaliza dados do usuário a partir do token/contexto
  const rawFirstName =
    (user?.firstName as string | undefined) ??
    ((user?.fullName as string | undefined)?.split(" ")[0] ?? "");
  const rawLastName = (user?.lastName as string | undefined) ?? "";
  const email = (user?.email as string | undefined) ?? "";

  const firstName = fixEncoding(rawFirstName);
  const lastName = fixEncoding(rawLastName);

  const isLoggedIn = !!user;

  // Busca categorias reais da API (mesmas da homepage / sidebar)
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        setLoadingCategories(true);

        const res = await fetch(`${API_BASE_URL}/api/admin/categories`, {
          cache: "no-store",
        });

        if (!res.ok) {
          console.error(
            "Erro ao carregar categorias no header:",
            res.status,
            await res.text(),
          );
          return;
        }

        const data: CategoryApi[] = await res.json();

        const mapped = data
          .filter((c) => c.isActive) // Header só mostra ativas
          .sort(
            (a, b) =>
              new Date(a.createdAt).getTime() -
              new Date(b.createdAt).getTime(),
          )
          .map((c) => ({
            id: c.id,
            name: c.name,
            slug: generateSlug(c.name),
            createdAt: c.createdAt,
          }));

        setCategories(mapped);
      } catch (error) {
        console.error("Erro ao buscar categorias no header:", error);
      } finally {
        setLoadingCategories(false);
      }
    };

    void fetchCategories();
  }, []);

  return (
    <>
      <PromoBanner />
      <header className="sticky top-0 z-50 bg-background border-b border-border">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-20 gap-4">
            {/* Logo */}
            <Link href="/" className="flex items-center space-x-2 shrink-0">
              <div className="w-8 h-8 bg-primary rounded-sm flex items-center justify-center">
                <span className="text-primary-foreground font-bold text-lg">
                  M
                </span>
              </div>
              <span className="hidden sm:inline text-xl font-light tracking-tight text-foreground">
                Memoriza
              </span>
            </Link>

            {/* Search (desktop) */}
            <div className="flex-1 max-w-2xl hidden md:block">
              <div className="relative">
                <Search
                  className="absolute left-4 top-1/2 -translate-y-1/2 text-foreground/50"
                  size={18}
                />
                <input
                  type="text"
                  placeholder="Buscar produtos, categorias..."
                  className="w-full pl-12 pr-4 py-3 bg-muted border border-border rounded-lg text-sm text-foreground placeholder:text-foreground/50 focus:outline-none focus:border-accent transition-colors"
                />
              </div>
            </div>

            {/* Right Icons */}
            <div className="flex items-center space-x-6 shrink-0">
              {/* Search mobile icon */}
              <button className="md:hidden text-foreground hover:text-accent transition-colors">
                <Search size={20} />
              </button>

              {/* User / Profile menu */}
              <div className="relative">
                <button
                  onClick={() => setProfileMenuOpen(!profileMenuOpen)}
                  className="text-foreground hover:text-accent transition-colors flex items-center gap-2"
                >
                  <User size={20} />
                  {isLoggedIn && firstName && (
                    <span className="hidden sm:inline text-sm font-medium">
                      {firstName}
                    </span>
                  )}
                </button>

                {profileMenuOpen && (
                  <div className="absolute right-0 top-full mt-2 w-56 bg-background border border-border rounded-lg shadow-lg py-2 z-50">
                    {isLoggedIn ? (
                      <>
                        {/* Cabeçalho com nome e email */}
                        <div className="px-4 py-2 border-b border-border">
                          <p className="font-medium text-sm">
                            {firstName} {lastName}
                          </p>
                          {email && (
                            <p className="text-xs text-foreground/60">
                              {email}
                            </p>
                          )}
                        </div>

                        {isAdmin ? (
                          <>
                            <Link
                              href="/admin"
                              className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors text-accent font-medium"
                              onClick={() => setProfileMenuOpen(false)}
                            >
                              <Settings size={16} />
                              Painel Administrativo
                            </Link>

                            <button
                              onClick={() => {
                                logout();
                                setProfileMenuOpen(false);
                              }}
                              className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors w-full text-left text-red-500 border-t border-border mt-2 pt-2"
                            >
                              <LogOut size={16} />
                              Sair
                            </button>
                          </>
                        ) : (
                          <>
                            <Link
                              href="/minha-conta/perfil"
                              className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors"
                              onClick={() => setProfileMenuOpen(false)}
                            >
                              <User size={16} />
                              Meu Perfil
                            </Link>

                            <Link
                              href="/minha-conta/enderecos"
                              className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors"
                              onClick={() => setProfileMenuOpen(false)}
                            >
                              <MapPin size={16} />
                              Endereços
                            </Link>

                            <Link
                              href="/minha-conta/pedidos"
                              className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors"
                              onClick={() => setProfileMenuOpen(false)}
                            >
                              <Package size={16} />
                              Meus Pedidos
                            </Link>

                            <button
                              onClick={() => {
                                logout();
                                setProfileMenuOpen(false);
                              }}
                              className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors w-full text-left text-red-500 border-t border-border mt-2 pt-2"
                            >
                              <LogOut size={16} />
                              Sair
                            </button>
                          </>
                        )}
                      </>
                    ) : (
                      <>
                        <Link
                          href="/auth/login"
                          className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors"
                          onClick={() => setProfileMenuOpen(false)}
                        >
                          <User size={16} />
                          Entrar
                        </Link>
                        <Link
                          href="/auth/login"
                          className="flex items-center gap-3 px-4 py-2 text-sm hover:bg-muted transition-colors"
                          onClick={() => setProfileMenuOpen(false)}
                        >
                          Criar conta
                        </Link>
                      </>
                    )}
                  </div>
                )}
              </div>
              {/* End user menu */}

              {/* Cart icon */}
              <Link
                href="/cart"
                className="relative text-foreground hover:text-accent transition-colors"
              >
                <ShoppingBag size={20} />
                <span className="absolute -top-2 -right-2 bg-accent text-accent-foreground text-xs font-bold w-5 h-5 rounded-full flex items-center justify-center">
                  0
                </span>
              </Link>

              {/* Mobile menu button */}
              <button
                className="md:hidden text-foreground hover:text-accent transition-colors"
                onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              >
                <Menu size={20} />
              </button>
            </div>
          </div>

          {/* Mobile Menu */}
          {mobileMenuOpen && (
            <nav className="md:hidden pb-4 space-y-3">
              {/* Mobile search */}
              <div className="relative mb-4">
                <Search
                  className="absolute left-4 top-1/2 -translate-y-1/2 text-foreground/50"
                  size={18}
                />
                <input
                  type="text"
                  placeholder="Buscar produtos..."
                  className="w-full pl-12 pr-4 py-3 bg-muted border border-border rounded-lg text-sm"
                />
              </div>

              {isAdmin && (
                <Link
                  href="/admin"
                  className="block text-accent font-medium hover:text-accent/80 transition-colors text-sm py-2 border-b border-border mb-2"
                  onClick={() => setMobileMenuOpen(false)}
                >
                  Painel Administrativo
                </Link>
              )}

              {loadingCategories && (
                <p className="text-xs text-foreground/60">
                  Carregando categorias...
                </p>
              )}

              {!loadingCategories &&
                categories.map((category) => (
                  <Link
                    key={category.id}
                    href={`/products?category=${category.slug}`}
                    className="block text-foreground hover:text-accent transition-colors text-sm font-medium"
                    onClick={() => setMobileMenuOpen(false)}
                  >
                    {category.name}
                  </Link>
                ))}

              {!loadingCategories && categories.length === 0 && (
                <p className="text-xs text-foreground/60">
                  Nenhuma categoria encontrada.
                </p>
              )}
            </nav>
          )}
        </div>
      </header>

      {/* Barra de categorias abaixo do header (desktop) */}
      <div className="bg-muted/50 border-b border-border">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <nav className="hidden md:flex items-center justify-center gap-6 py-3 overflow-x-auto">
            {loadingCategories && (
              <span className="text-xs text-foreground/60">
                Carregando categorias...
              </span>
            )}

            {!loadingCategories &&
              categories.map((category) => (
                <Link
                  key={category.id}
                  href={`/products?category=${category.slug}`}
                  className="text-foreground/80 hover:text-accent transition-colors text-sm font-medium whitespace-nowrap"
                >
                  {category.name}
                </Link>
              ))}

            {!loadingCategories && categories.length === 0 && (
              <span className="text-xs text-foreground/60">
                Nenhuma categoria disponível.
              </span>
            )}
          </nav>
        </div>
      </div>
    </>
  );
}