"use client"

import { useState, useEffect } from "react"
import { ChevronLeft, ChevronRight } from "lucide-react"

const slides = [
  {
    id: 1,
    title: "Kits com até 30% OFF",
    subtitle: "Aproveite nossas ofertas especiais em kits personalizados",
    cta: "Ver Ofertas",
    image: "/promotional-banner-elegant-gift-boxes-with-ribbon.jpg",
    bgColor: "bg-primary/5",
  },
  {
    id: 2,
    title: "Papelaria Personalizada",
    subtitle: "Cartões, convites e muito mais com a sua marca",
    cta: "Explorar",
    image: "/elegant-stationery-personalized-cards-invitations.jpg",
    bgColor: "bg-accent/5",
  },
  {
    id: 3,
    title: "Novidades em Embalagens",
    subtitle: "Embalagens premium para elevar sua apresentação",
    cta: "Conferir",
    image: "/luxury-packaging-boxes-premium-gift-wrap.jpg",
    bgColor: "bg-muted",
  },
]

export function HeroCarousel() {
  const [current, setCurrent] = useState(0)

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrent((prev) => (prev + 1) % slides.length)
    }, 5000)
    return () => clearInterval(timer)
  }, [])

  const goTo = (index: number) => setCurrent(index)
  const prev = () => setCurrent((prev) => (prev - 1 + slides.length) % slides.length)
  const next = () => setCurrent((prev) => (prev + 1) % slides.length)

  return (
    <div className="relative w-full overflow-hidden">
      {/* Slides */}
      <div
        className="flex transition-transform duration-500 ease-out"
        style={{ transform: `translateX(-${current * 100}%)` }}
      >
        {slides.map((slide) => (
          <div key={slide.id} className={`w-full flex-shrink-0 ${slide.bgColor}`}>
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
              <div className="flex flex-col md:flex-row items-center gap-8 py-12 md:py-16">
                {/* Content */}
                <div className="flex-1 text-center md:text-left">
                  <h2 className="text-3xl md:text-4xl lg:text-5xl font-light text-foreground mb-4 text-balance">
                    {slide.title}
                  </h2>
                  <p className="text-lg text-foreground/70 mb-6 max-w-lg">{slide.subtitle}</p>
                  <button className="px-8 py-3 bg-primary text-primary-foreground rounded-lg font-medium hover:opacity-90 transition-opacity">
                    {slide.cta}
                  </button>
                </div>
                {/* Image */}
                <div className="flex-1 w-full max-w-xl">
                  <img
                    src={slide.image || "/placeholder.svg"}
                    alt={slide.title}
                    className="w-full h-64 md:h-80 object-cover rounded-xl"
                  />
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Navigation Arrows */}
      <button
        onClick={prev}
        className="absolute left-4 top-1/2 -translate-y-1/2 w-10 h-10 bg-background backdrop-blur-sm rounded-full flex items-center justify-center text-foreground hover:bg-background transition-colors shadow-lg"
      >
        <ChevronLeft size={20} />
      </button>
      <button
        onClick={next}
        className="absolute right-4 top-1/2 -translate-y-1/2 w-10 h-10 bg-background/80 backdrop-blur-sm rounded-full flex items-center justify-center text-foreground hover:bg-background transition-colors shadow-lg"
      >
        <ChevronRight size={20} />
      </button>

      {/* Dots */}
      <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex gap-2">
        {slides.map((_, idx) => (
          <button
            key={idx}
            onClick={() => goTo(idx)}
            className={`w-2 h-2 rounded-full transition-all ${
              idx === current ? "bg-primary w-6" : "bg-foreground/30 hover:bg-foreground/50"
            }`}
          />
        ))}
      </div>
    </div>
  )
}
