"use client";

import { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import { ChevronRight } from "lucide-react";

import { Header } from "@/components/header";
import { Footer } from "@/components/footer";
import { HeroCarousel } from "@/components/hero-carousel";
import { WhatsAppButton } from "@/components/whatsapp-button";

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105";

// ===== Tipos da API =====
interface CategoryApi {
  id: string;
  name: string;
  description: string | null;
  isActive: boolean;
  createdAt: string;
}

interface SizeApi {
  id: number;
  name: string;
  isActive: boolean;
  createdAt: string;
}

interface ColorApi {
  id: number;
  name: string;
  hexCode: string | null;
  isActive: boolean;
  createdAt: string;
}

interface ProductImageApi {
  id: string;
  productId: string;
  url: string;
  altText: string | null;
  isPrimary: boolean;
  displayOrder: number;
  createdAt: string;
}

interface ProductApi {
  id: string;
  categoryId: string;
  name: string;
  description: string | null;
  price: number;
  promotionalPrice: number | null;
  sizeIds: number[];
  colorIds: number[];
  isPersonalizable: boolean;
  isActive: boolean;
  createdAt: string;
  images: ProductImageApi[];
}

// ===== Modelos de tela =====
interface Category {
  id: string;
  name: string;
  slug: string;
}

interface Size {
  id: number;
  name: string;
}

interface Color {
  id: number;
  name: string;
  hex: string | null;
}

interface Product {
  id: string;
  name: string;
  price: number;
  promotionalPrice?: number | null;
  imageUrl: string | null;
  sizeIds: number[];
  colorIds: number[];
}

// Helper para slug
const generateSlug = (nome: string) =>
  nome
    .toLowerCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/(^-|-$)/g, "");

const formatCurrency = (value: number) =>
  value.toLocaleString("pt-BR", {
    style: "currency",
    currency: "BRL",
  });

export default function Home() {
  const [categories, setCategories] = useState<Category[]>([]);
  const [sizes, setSizes] = useState<Size[]>([]);
  const [colors, setColors] = useState<Color[]>([]);
  const [products, setProducts] = useState<Product[]>([]);

  const [loadingSidebar, setLoadingSidebar] = useState(true);
  const [loadingProducts, setLoadingProducts] = useState(true);

  const [selectedSizeIds, setSelectedSizeIds] = useState<number[]>([]);
  const [selectedColorIds, setSelectedColorIds] = useState<number[]>([]);
  const [showAllSizes, setShowAllSizes] = useState(false);
  const [showAllColors, setShowAllColors] = useState(false);

  // ===== Fetch categorias, tamanhos, cores e produtos =====
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoadingSidebar(true);
        setLoadingProducts(true);

        const [catRes, sizeRes, colorRes, productRes] = await Promise.all([
          fetch(`${API_BASE_URL}/api/admin/categories`, { cache: "no-store" }),
          fetch(`${API_BASE_URL}/api/admin/sizes`, { cache: "no-store" }),
          fetch(`${API_BASE_URL}/api/admin/colors`, { cache: "no-store" }),
          fetch(`${API_BASE_URL}/api/admin/products`, { cache: "no-store" }),
        ]);

        // Categorias
        if (catRes.ok) {
          const data: CategoryApi[] = await catRes.json();
          const mapped: Category[] = data
            .filter((c) => c.isActive)
            .sort(
              (a, b) =>
                new Date(a.createdAt).getTime() -
                new Date(b.createdAt).getTime(),
            )
            .map((c) => ({
              id: c.id,
              name: c.name,
              slug: generateSlug(c.name),
            }));
          setCategories(mapped);
        }

        // Tamanhos
        if (sizeRes.ok) {
          const data: SizeApi[] = await sizeRes.json();
          const mappedSizes: Size[] = data
            .filter((s) => s.isActive)
            .map((s) => ({ id: s.id, name: s.name }));
          setSizes(mappedSizes);
        }

        // Cores
        if (colorRes.ok) {
          const data: ColorApi[] = await colorRes.json();
          const mappedColors: Color[] = data
            .filter((c) => c.isActive)
            .map((c) => ({
              id: c.id,
              name: c.name,
              hex: c.hexCode,
            }));
          setColors(mappedColors);
        }

        // Produtos
        if (productRes.ok) {
          const data: ProductApi[] = await productRes.json();

          const mappedProducts: Product[] = data
            .filter((p) => p.isActive) // só produtos ativos
            .map((p) => {
              const primaryImage =
                p.images.find((img) => img.isPrimary) ?? p.images[0];

              return {
                id: p.id,
                name: p.name,
                price: p.price,
                promotionalPrice: p.promotionalPrice,
                imageUrl: primaryImage?.url ?? null,
                sizeIds: p.sizeIds ?? [],
                colorIds: p.colorIds ?? [],
              };
            });

          setProducts(mappedProducts);
        }
      } catch (error) {
        console.error("Erro ao carregar dados da homepage:", error);
      } finally {
        setLoadingSidebar(false);
        setLoadingProducts(false);
      }
    };

    void fetchData();
  }, []);

  // Contagem de produtos por tamanho/cor (estilo "15x22cm (3)")
  const sizeCounts = useMemo(() => {
    const counts: Record<number, number> = {};
    products.forEach((p) => {
      p.sizeIds.forEach((id) => {
        counts[id] = (counts[id] || 0) + 1;
      });
    });
    return counts;
  }, [products]);

  const colorCounts = useMemo(() => {
    const counts: Record<number, number> = {};
    products.forEach((p) => {
      p.colorIds.forEach((id) => {
        counts[id] = (counts[id] || 0) + 1;
      });
    });
    return counts;
  }, [products]);

  // Toggle seleção de filtros
  const toggleSize = (id: number) => {
    setSelectedSizeIds((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id],
    );
  };

  const toggleColor = (id: number) => {
    setSelectedColorIds((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id],
    );
  };

  // Helpers pra exibir nomes de tamanho/cor do produto
  const getSizeLabel = (product: Product) => {
    const names = product.sizeIds
      .map((id) => sizes.find((s) => s.id === id)?.name)
      .filter(Boolean);
    return names.join(", ");
  };

  const getColorLabel = (product: Product) => {
    const names = product.colorIds
      .map((id) => colors.find((c) => c.id === id)?.name)
      .filter(Boolean);
    return names.join(", ");
  };

  // Produtos filtrados por tamanho/cor
  const filteredProducts = useMemo(() => {
    return products.filter((p) => {
      const matchSize =
        selectedSizeIds.length === 0 ||
        p.sizeIds.some((id) => selectedSizeIds.includes(id));

      const matchColor =
        selectedColorIds.length === 0 ||
        p.colorIds.some((id) => selectedColorIds.includes(id));

      return matchSize && matchColor;
    });
  }, [products, selectedSizeIds, selectedColorIds]);

  // Tamanhos/cores visíveis (para "Ver todos")
  const visibleSizes = showAllSizes ? sizes : sizes.slice(0, 7);
  const visibleColors = showAllColors ? colors : colors.slice(0, 7);

  return (
    <div className="min-h-screen flex flex-col">
      <Header />

      <HeroCarousel />

      {/* Main Content */}
      <div className="flex-1 flex">
        {/* Sidebar (Categorias + Tamanho + Cor) */}
        <aside className="hidden lg:block w-72 bg-muted/30 border-r border-border px-6 py-8 overflow-y-auto">
          {/* Categorias */}
          <div className="mb-8">
            <h2 className="text-sm font-medium text-foreground uppercase tracking-widest mb-4">
              Categorias
            </h2>

            {loadingSidebar && categories.length === 0 ? (
              <p className="text-xs text-foreground/60">
                Carregando categorias...
              </p>
            ) : (
              <nav className="space-y-2">
                <Link
                  href="/products"
                  className="flex items-center justify-between px-4 py-3 rounded-lg transition-all bg-primary text-primary-foreground"
                >
                  <span className="text-sm">Todos os Produtos</span>
                  <ChevronRight size={16} />
                </Link>

                {categories.map((category) => (
                  <Link
                    key={category.id}
                    href={`/products?category=${category.slug}`}
                    className="group flex items-center justify-between px-4 py-3 rounded-lg transition-all text-foreground bg-primary/3 hover:bg-accent/10 hover:text-accent"
                  >
                    <span className="text-sm">{category.name}</span>
                    <ChevronRight
                      size={16}
                      className="opacity-0 group-hover:opacity-100 transition-opacity"
                    />
                  </Link>
                ))}
              </nav>
            )}
          </div>

          {/* Tamanho e Filtrar por */}
          <div className="mb-8">
            <h2 className="text-sm font-medium text-foreground uppercase tracking-widest mb-4">
              Filtrar por
            </h2>
            <h3 className="text-base font-medium text-foreground mb-3">
              Tamanho
            </h3>

            {visibleSizes.map((size) => (
              <label
                key={size.id}
                className="flex items-center gap-2 py-1.5 text-base text-foreground cursor-pointer"
              >
                <input
                  type="checkbox"
                  className="h-5 w-5 rounded border-border text-primary focus:ring-primary"
                  checked={selectedSizeIds.includes(size.id)}
                  onChange={() => toggleSize(size.id)}
                />
                <span className="flex-1">
                  {size.name}{" "}
                  <span className="text-sm text-foreground/60">
                    ({sizeCounts[size.id] ?? 0})
                  </span>
                </span>
              </label>
            ))}

            {sizes.length > visibleSizes.length && (
              <button
                type="button"
                onClick={() => setShowAllSizes(true)}
                className="mt-3 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-primary/30 text-primary bg-background hover:bg-primary/5 transition-colors"
              >
                Ver todos
              </button>
            )}

            {showAllSizes && sizes.length > 7 && (
              <button
                type="button"
                onClick={() => setShowAllSizes(false)}
                className="mt-2 ml-1 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-border text-foreground/70 bg-background hover:bg-muted transition-colors"
              >
                Ver menos
              </button>
            )}
          </div>

          {/* Cor */}
          <div className="mb-4">
            <h3 className="text-base font-medium text-foreground mb-3">Cor</h3>

            {visibleColors.map((color) => (
              <label
                key={color.id}
                className="flex items-center gap-2 py-1.5 text-base text-foreground cursor-pointer"
              >
                <input
                  type="checkbox"
                  className="h-5 w-5 rounded border-border text-primary focus:ring-primary"
                  checked={selectedColorIds.includes(color.id)}
                  onChange={() => toggleColor(color.id)}
                />
                <span className="flex items-center gap-2 flex-1">
                  <span
                    className="w-5 h-5 rounded-full border border-border"
                    style={{
                      backgroundColor: color.hex || "#ffffff",
                    }}
                  />
                  <span>
                    {color.name}{" "}
                    <span className="text-sm text-foreground/60">
                      ({colorCounts[color.id] ?? 0})
                    </span>
                  </span>
                </span>
              </label>
            ))}

            {colors.length > visibleColors.length && (
              <button
                type="button"
                onClick={() => setShowAllColors(true)}
                className="mt-3 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-primary/30 text-primary bg-background hover:bg-primary/5 transition-colors"
              >
                Ver todos
              </button>
            )}

            {showAllColors && colors.length > 7 && (
              <button
                type="button"
                onClick={() => setShowAllColors(false)}
                className="mt-2 ml-1 inline-flex items-center px-3 py-1.5 text-xs rounded-full border border-border text-foreground/70 bg-background hover:bg-muted transition-colors"
              >
                Ver menos
              </button>
            )}
          </div>
        </aside>

        {/* Main Content Area */}
        <main className="flex-1 px-4 sm:px-6 lg:px-8 py-12">
          <div className="max-w-6xl mx-auto">
            {/* Header Section */}
            <div className="mb-12">
              <p className="text-accent font-medium text-sm uppercase tracking-widest mb-2">
                Catálogo Premium
              </p>
              <h1 className="text-4xl md:text-5xl font-light text-foreground mb-4 text-balance">
                Produtos Personalizados Únicos
              </h1>
              <p className="text-lg text-foreground/70 max-w-2xl">
                Explore nossa coleção exclusiva de produtos premium, cada um
                pronto para ser personalizado conforme sua criatividade.
              </p>
            </div>

            {/* Filter/Sort Bar (top) */}
            <div className="flex flex-col sm:flex-row gap-4 items-start sm:items-center justify-between mb-8">
              <div className="flex gap-2 flex-wrap">
                <button className="px-4 py-2 rounded-lg border border-border bg-background text-foreground hover:border-accent transition-colors text-sm font-medium">
                  Mais Vendidos
                </button>
                <button className="px-4 py-2 rounded-lg border border-border bg-background text-foreground hover:border-accent transition-colors text-sm font-medium">
                  Novidades
                </button>
                <button className="px-4 py-2 rounded-lg border border-border bg-background text-foreground hover:border-accent transition-colors text-sm font-medium">
                  Promoções
                </button>
              </div>
              <select className="px-4 py-2 rounded-lg border border-border bg-background text-foreground text-sm">
                <option>Ordenar por: Relevância</option>
                <option>Menor Preço</option>
                <option>Maior Preço</option>
                <option>Mais Recentes</option>
              </select>
            </div>

            {/* Products Grid */}
            {loadingProducts && products.length === 0 ? (
              <div className="py-12 text-center text-foreground/60">
                Carregando produtos...
              </div>
            ) : (
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                {filteredProducts.map((product) => {
                  const sizeLabel = getSizeLabel(product);
                  const colorLabel = getColorLabel(product);

                  const basePrice = product.price;
                  const finalPrice =
                    product.promotionalPrice && product.promotionalPrice > 0
                      ? product.promotionalPrice
                      : basePrice;

                  return (
                    <Link
                      key={product.id}
                      href={`/products/${product.id}`}
                      className="group cursor-pointer"
                    >
                      <div className="bg-muted rounded-lg overflow-hidden aspect-square mb-4">
                        <img
                          src={product.imageUrl || "/placeholder.svg"}
                          alt={product.name}
                          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                        />
                      </div>
                      <h3 className="font-medium text-foreground group-hover:text-accent transition-colors">
                        {product.name}
                      </h3>
                      {(sizeLabel || colorLabel) && (
                        <p className="text-xs text-foreground/60 mt-1">
                          {sizeLabel && <span>{sizeLabel}</span>}
                          {sizeLabel && colorLabel && " • "}
                          {colorLabel && <span>{colorLabel}</span>}
                        </p>
                      )}
                      <div className="flex flex-col mt-2">
                        <div className="flex items-center gap-2">
                          <p className="text-lg font-medium text-primary">
                            {formatCurrency(finalPrice)}
                          </p>
                          {product.promotionalPrice &&
                            product.promotionalPrice > 0 &&
                            product.promotionalPrice < basePrice && (
                              <p className="text-sm text-foreground/50 line-through">
                                {formatCurrency(basePrice)}
                              </p>
                            )}
                        </div>
                        <p className="text-xs text-foreground/70">
                          ou 12x de{" "}
                          {formatCurrency(finalPrice / 12)}{" "}
                          sem juros
                        </p>
                      </div>
                    </Link>
                  );
                })}

                {filteredProducts.length === 0 && !loadingProducts && (
                  <div className="col-span-full py-12 text-center text-foreground/60">
                    Nenhum produto encontrado com os filtros selecionados.
                  </div>
                )}
              </div>
            )}

            {/* Load More (placeholder – ainda sem paginação real) */}
            <div className="text-center mt-12">
              <button className="px-8 py-3 border-2 border-primary text-primary rounded-lg font-medium hover:bg-primary/5 transition-colors">
                Carregar Mais Produtos
              </button>
            </div>
          </div>
        </main>
      </div>

      <Footer />
      <WhatsAppButton />
    </div>
  );
}